import { getAuthUserData } from "./authReduser";

const INITALIZED_SUCCESS = "SET_INITALIZED_SUCCESS";

let initialState = {
  initialised: false,
};
const appReduser = (state = initialState, action) => {
  switch (action.type) {
    case INITALIZED_SUCCESS:
      return {
        ...state,
        initialised: true,
      };

    default:
      return state;
  }
};
export const initalisedSuccess = () => {
  return { type: INITALIZED_SUCCESS };
};
export const initialise = () => {
  return (dispatch) => {
    let promise = dispatch(getAuthUserData());
    promise.then(() => {
      dispatch(initalisedSuccess());
    });
  };
};

export default appReduser;
