import React, { useState } from "react";

import s from "./Paginator.module.css";
import cn from "classnames";
const Paginator = (props) => {
  let pageCount = Math.ceil(props.totalItemsCount / props.pageSize);
  let pages = [];
  for (let i = 1; i <= pageCount; i++) {
    pages.push(i);
  }
  let portionCount = Math.ceil(pageCount / props.portionSize);
  let [portionNumber, setPortionNumber] = useState(1);
  let leftPortionPageNumber = (portionNumber - 1) * props.portionSize;
  let rightPortionPageSize = portionNumber * props.portionSize;

  return (
    <div className={s.paginator}>
      {portionNumber > 1 && (
        <button
          onClick={() => {
            setPortionNumber(portionNumber - 1);
          }}
        >
          Prev
        </button>
      )}

      {pages
        .filter((p) => p > leftPortionPageNumber && p <= rightPortionPageSize)
        .map((p) => {
          return (
            <span
              key={p}
              onClick={() => {
                props.onPageChanged(p);
              }}
              className={cn(
                { [s.selectedCount]: props.currentPage === p },
                s.pageNumber
              )}
            >
              {p}
            </span>
          );
        })}
      {portionCount > portionNumber && (
        <button
          onClick={() => {
            setPortionNumber(portionNumber + 1);
          }}
        >
          Next
        </button>
      )}
    </div>
  );
};

export default Paginator;
