import React, { useState } from "react";
import Preloader from "../../common/Preloader/Preloader";
import s from "./ProfileInfo.module.css";

import ProfileStatusWithHook from "./profileStatusWithHook";
import userfoto from "../../../assets/images/user.png";

import ProfileDataReduxForm from "./ProfileDataForm";
const ProfileInfo = (props) => {
  if (!props.profile) {
    return <Preloader />;
  }
  const onMainFotoSelected = (e) => {
    if (e.target.files.length != 0) {
      props.savePhoto(e.target.files[0]);
    }
  };
  const onSubmit = (formData) => {
    props.saveProfile(formData).then(() => {
      setEditMode(false);
    });
  };
  const [editMode, setEditMode] = useState(false);

  return (
    <div>
      <div>
        <img
          src="https://i.pinimg.com/originals/72/42/59/7242596ac964d4ff2689196f854dc16c.jpg"
          alt=""
        />
      </div>
      <div className={s.descriptionBlock}>
        <img
          src={props.profile.photos.large || userfoto}
          className={s.mainFoto}
        />
        {props.isOwner && <input type={"file"} onChange={onMainFotoSelected} />}
      </div>

      {editMode ? (
        <ProfileDataReduxForm
          initialValues={props.profile}
          onSubmit={onSubmit}
          profile={props.profile}
        />
      ) : (
        <ProfileData
          profile={props.profile}
          isOwner={props.isOwner}
          goEditMode={() => {
            setEditMode(true);
          }}
        />
      )}
      <ProfileStatusWithHook
        status={props.status}
        updateStatus={props.updateStatus}
      />
    </div>
  );
};

const ProfileData = (props) => {
  return (
    <div>
      {props.isOwner && <button onClick={props.goEditMode}>Edit</button>}
      <div>
        <b>Full name: </b>
        {props.profile.fullName}
      </div>
      <div>
        <b>Looking for a job: </b>
        {props.profile.lookingForAJob ? "yes" : "no"}
      </div>
      {props.profile.lookingForAJob && (
        <div>
          <b>Looking for a job description: </b>
          {props.profile.lookingForAJobDescription}
        </div>
      )}
      <div>
        <b>About me: </b>
        {props.profile.aboutMe}
      </div>
      <div>
        <b>Contacts: </b>
        {Object.keys(props.profile.contacts).map((key) => {
          return (
            <Contact
              key={key}
              contactTitle={key}
              contactValue={props.profile.contacts[key]}
            />
          );
        })}
      </div>
    </div>
  );
};

const Contact = ({ contactTitle, contactValue }) => {
  return (
    <div className={s.contact}>
      <b>{contactTitle}</b>: {contactValue}
    </div>
  );
};
export default ProfileInfo;
