import { stopSubmit } from "redux-form";
import { profileAPI, usersAPI } from "../API/api";

const ADD_POST = "ADD-POST";

const SET_USER_PROFILE = "SET-USER-PROFILE";
const SET_STATUS = "SET-STATUS";
const SET_PHOTO = "SET-PHOTO";
let initialState = {
  posts: [
    { id: 1, message: "Hi, fdghttrhrth", licesCount: 12 },
    { id: 2, message: "jgihhegj ijtgiekje jghejelmg", licesCount: 11 },
  ],

  profile: null,
  status: "",
};
const profileReduser = (state = initialState, action) => {
  switch (action.type) {
    case ADD_POST:
      let newPost = {
        id: 5,
        message: action.newPostText,
        licesCount: 0,
      };
      return { ...state, posts: [...state.posts, newPost] };

    case SET_USER_PROFILE:
      return { ...state, profile: action.profile };
    case SET_STATUS:
      return { ...state, status: action.status };
    case SET_PHOTO:
      return { ...state, profile: { ...state.profile, photos: action.photos } };

    default:
      return state;
  }
};
export const AddPostActionCreater = (newPostText) => {
  return {
    type: ADD_POST,
    newPostText,
  };
};

export const setUserProfile = (profile) => {
  return {
    type: SET_USER_PROFILE,
    profile,
  };
};
export const setStatus = (status) => {
  return {
    type: SET_STATUS,
    status,
  };
};
export const setPhoto = (file) => {
  return {
    type: SET_PHOTO,
    photos: file,
  };
};
export const getUserProfile = (userId) => {
  return async (dispatch) => {
    let response = await usersAPI.getprofile(userId);

    dispatch(setUserProfile(response.data));
  };
};
export const getStatus = (userId) => async (dispatch) => {
  let response = await profileAPI.getStatus(userId);
  if (response.data !== null) {
    dispatch(setStatus(response.data));
  } else {
    dispatch(setStatus("non status"));
  }
};

export const updateStatus = (status) => async (dispatch) => {
  let response = await profileAPI.updateStatus(status);
  if (response.data.resultCode === 0) {
    dispatch(setStatus(status));
  }
};
export const savePhoto = (file) => async (dispatch) => {
  let response = await profileAPI.savePhoto(file);
  if (response.data.resultCode === 0) {
    dispatch(setPhoto(response.data.data.photos));
  }
};

export const saveProfile = (formData) => async (dispatch, getState) => {
  const userId = getState().auth.id;
  const elementExtracted = (str) => {
    let arr = str.split(/[^a-z]/i);
    let element;
    for (let i = 0; i < arr.length; i++) {
      if (arr[i] == "Contacts") {
        element = arr[i + 2];
      }
    }
    return element[0].toLowerCase() + element.substring(1);
  };
  let response = await profileAPI.saveProfile(formData);
  if (response.data.resultCode === 0) {
    dispatch(getUserProfile(userId));
  } else {
    let messages =
      response.data.messages.length > 0
        ? response.data.messages[0]
        : "Some Error";

    dispatch(
      stopSubmit("edit-profile", {
        contacts: { [elementExtracted(response.data.messages[0])]: messages },
      })
    );
    return Promise.reject(response.data.messages[0]);
  }
};

export default profileReduser;
