import React from "react";
import { NavLink } from "react-router-dom";

import s from "./Navbar.module.css";
const Navbar = (props) => {
  return (
    <nav className={s.nav}>
      <div className={s.item}>
        <NavLink
          to="/profile"
          className={(navData) => (navData.isActive ? s.active : s.item)}
        >
          Profile
        </NavLink>
      </div>
      <div className={s.item}>
        <NavLink
          to="/messages"
          className={(navData) => (navData.isActive ? s.active : s.item)}
        >
          Messages
        </NavLink>
      </div>
      <div className={s.item}>
        <NavLink
          to="/musice"
          className={(navData) => (navData.isActive ? s.active : s.item)}
        >
          Musice
        </NavLink>
      </div>
      <div className={s.item}>
        <NavLink
          to="/new"
          className={(navData) => (navData.isActive ? s.active : s.item)}
        >
          New
        </NavLink>
      </div>
      <div className={s.item}>
        <NavLink
          to="/setting"
          className={(navData) => (navData.isActive ? s.active : s.item)}
        >
          Setting
        </NavLink>
      </div>
      <div className={s.item}>
        <NavLink
          to="/friends"
          className={(navData) => (navData.isActive ? s.active : s.item)}
        >
          {/*  <Friends state={props.state.friends} /> */}
        </NavLink>
      </div>
      <div className={s.item}>
        <NavLink
          to="/users"
          className={(navData) => (navData.isActive ? s.active : s.item)}
        >
          Users
        </NavLink>
      </div>
    </nav>
  );
};

export default Navbar;
