import { usersAPI } from "../API/api";
import { updateObjectInArray } from "../utils/objectHelpers";

const FOLLOW = "FOLLOW";
const UNFOLLOW = "UNFOLLOW";
const SET_USERS = "SET_USERS";
const UP_DATA_CURRENT_PAGE = "UP_DATA_CURRENT_PAGE";
const SET_TOTAL_USER_COUNT = "SET_TOTAL_USER_COUNT";
const TOGGLE_IS_FETCHING = "TOGGLE_IS_FETCHING";
const TOGGLE_IN_PROGRESS = "TOGGLE_IN_PROGRESS";

let initialState = {
  users: [],
  pageSize: 10,
  portionSize: 10,
  totalUsersCount: 0,
  currentPage: 1,
  isFetching: false,
  followingInProgress: [],
};
const usersReduser = (state = initialState, action) => {
  switch (action.type) {
    case FOLLOW:
      return {
        ...state,
        users: updateObjectInArray(state.users, action.userId, "id", {
          followed: true,
        }),
        /* 
        users: state.users.map((u) => {
          if (u.id === action.userId) {
            return { ...u, followed: true };
          }
          return u;
        }), */
      };

    case UNFOLLOW:
      return {
        ...state,
        users: updateObjectInArray(state.users, action.userId, "id", {
          followed: false,
        }),
        /*  users: state.users.map((u) => {
          if (u.id === action.userId) {
            return { ...u, followed: false };
          }
          return u;
        }), */
      };
    case SET_USERS:
      return {
        ...state,
        users: [...action.users],
      };
    case UP_DATA_CURRENT_PAGE:
      return {
        ...state,
        currentPage: action.currentPage,
      };
    case SET_TOTAL_USER_COUNT:
      return {
        ...state,
        totalUsersCount: action.totalCount,
      };
    case TOGGLE_IS_FETCHING:
      return {
        ...state,
        isFetching: action.isFetching,
      };
    case TOGGLE_IN_PROGRESS:
      return {
        ...state,
        followingInProgress: action.isFetching
          ? [...state.followingInProgress, action.id]
          : state.followingInProgress.filter((id) => id !== action.id),
      };

    default:
      return state;
  }
};
export const followSuccess = (userId) => {
  return { type: FOLLOW, userId: userId };
};
export const unfollowSuccess = (userId) => {
  return { type: UNFOLLOW, userId: userId };
};
export const setUsers = (users) => {
  return { type: SET_USERS, users: users };
};
export const upDataCurrentPage = (currentPage) => {
  return { type: UP_DATA_CURRENT_PAGE, currentPage: currentPage };
};
export const setTotalUsersCount = (totalCount) => {
  return { type: SET_TOTAL_USER_COUNT, totalCount: totalCount };
};
export const toggleIsFetching = (isFetching) => {
  return { type: TOGGLE_IS_FETCHING, isFetching: isFetching };
};
export const toggleInProgress = (isFetching, id) => {
  return { type: TOGGLE_IN_PROGRESS, isFetching: isFetching, id: id };
};
export const requestUsers = (pageSize, currentPage) => {
  return async (dispatch) => {
    dispatch(toggleIsFetching(true));
    dispatch(upDataCurrentPage(currentPage));
    let data = await usersAPI.getUser(pageSize, currentPage);
    dispatch(toggleIsFetching(false));
    dispatch(setUsers(data.items));
    dispatch(setTotalUsersCount(data.totalCount));
  };
};
const followUnfollowFlow = async (
  dispatch,
  userId,
  APIMethod,
  ActionCreater
) => {
  dispatch(toggleInProgress(true, userId));

  let data = await APIMethod(userId);
  if (data.resultCode === 0) {
    dispatch(ActionCreater(userId));
  }
  dispatch(toggleInProgress(false, userId));
};

export const unfollow = (userId) => {
  return async (dispatch) => {
    let APIMethod = usersAPI.unfollowUser.bind(usersAPI);
    let ActionCreater = unfollowSuccess;
    followUnfollowFlow(dispatch, userId, APIMethod, ActionCreater);
  };
};
export const follow = (userId) => {
  return async (dispatch) => {
    let APIMethod = usersAPI.followUser.bind(usersAPI);
    let ActionCreater = followSuccess;
    return followUnfollowFlow(dispatch, userId, APIMethod, ActionCreater);
  };
};

