import React from "react";
import { connect } from "react-redux";
import {
  follow,
  requestUsers,
  unfollow,
  upDataCurrentPage,
  toggleInProgress,
} from "../../redux/usersReduser";
import Users from "./Users";

import Preloader from "../common/Preloader/Preloader";

import withAuthRedirectComponent from "../../hoc/AuthRedirect";
import { compose } from "redux";
import {
  getCurrentPage,
  getPortionSize,
  getFollowingInProgress,
  getIsFetching,
  getPageSize,
  getTotalUsersCount,
  getUsers,
} from "../../redux/usersSelectors";

class UsersComponent extends React.Component {
  /* constructor(props) {
    super(props);
  } */

  componentDidMount() {
    this.props.requestUsers(this.props.pageSize, this.props.currentPage);
  }
  onPageChanged = (p) => {
    this.props.requestUsers(this.props.pageSize, p);
  };
  render = () => {
    return (
      <>
        {this.props.isFetching ? <Preloader /> : null}
        <Users
          totalUsersCount={this.props.totalUsersCount}
          pageSize={this.props.pageSize}
          portionSize={this.props.portionSize}
          onPageChanged={this.onPageChanged}
          currentPage={this.props.currentPage}
          users={this.props.users}
          unfollow={this.props.unfollow}
          s
          follow={this.props.follow}
          followingInProgress={this.props.followingInProgress}
          toggleInProgress={this.props.toggleInProgress}
        />
      </>
    );
  };
}
const mapStateToProps = (state) => {
  return {
    users: getUsers(state),
    pageSize: getPageSize(state),
    portionSize: getPortionSize(state),
    totalUsersCount: getTotalUsersCount(state),
    currentPage: getCurrentPage(state),
    isFetching: getIsFetching(state),
    followingInProgress: getFollowingInProgress(state),
  };
};
/* const mapDispatchToProps = (dispatch) => {
  return {
    follow: (userId) => {
      dispatch(followAC(userId));
    },
    unfollow: (userId) => {
      dispatch(unfollowAC(userId));
    },
    setUsers: (users) => {
      dispatch(setUsersAC(users));
    },
    upDataCurrentPage: (currentPage) => {
      dispatch(upDataCurrentPageAC(currentPage));
    },
    setTotalUsersCount: (totalCount) => {
      dispatch(setTotalUsersCountAC(totalCount));
    },
    toggleIsFetching: (isFetching) => {
      dispatch(toggleIsFetchingAC(isFetching));
    },
  };
}; */
/* 
let AuthRedirectComponent = withAuthRedirectComponent(UsersComponent);
export default connect(mapStateToProps, {
  getUsers,
  upDataCurrentPage,
  follow,
  unfollow,
  toggleInProgress,
})(AuthRedirectComponent); */

export default compose(
  connect(mapStateToProps, {
    requestUsers: requestUsers,
    upDataCurrentPage,
    follow,
    unfollow,
    toggleInProgress,
  }),
  withAuthRedirectComponent
)(UsersComponent);
