const ADD_MESSAGE = "ADD-MESSAGE";

let initialState = {
  dialogs: [
    { id: 1, name: "Maxim" },
    { id: 2, name: "Ekaterina" },
    { id: 3, name: "Natalia" },
    { id: 4, name: "Dmitrii" },
    { id: 5, name: "Egor" },
    { id: 6, name: "Petr" },
    { id: 7, name: "Michail" },
    { id: 8, name: "Lubov" },
    { id: 9, name: "Koctia" },
    { id: 10, name: "Leonid" },
    { id: 11, name: "Anna" },
    { id: 12, name: "Boris" },
    { id: 13, name: "Kira" },
    { id: 14, name: "Mark" },
  ],
  messages: [
    { id: 1, message: "Hi, my name is Natalia" },
    { id: 2, message: "It's my love" },
    { id: 3, message: "it's my first project" },
  ],
};

const dialogsReduser = (state = initialState, action) => {
  switch (action.type) {
    case ADD_MESSAGE:
      let newMessage = {
        id: 4,
        message: action.newMessageBody,
      };
      return {
        ...state,
        messages: [...state.messages, newMessage],
      };

    default:
      return state;
  }
};

export const AddMessageActionCreater = (newMessageBody) => {
  return {
    type: ADD_MESSAGE,
    newMessageBody,
  };
};
export default dialogsReduser;
